.App {
  text-align: center;
  font-size: 14px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 1.5vmin);
  color: white;
}

table {
  border-collapse: collapse;
  margin: 20px 0;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px 12px;
  text-align: left;
}

th {
  background-color: #4caf50;
  color: white;
}

tr:nth-child(even) {
  background-color: #313131;
}

tr:hover {
  background-color: #ddd;
}

table {
  border-radius: 5px;
  overflow: hidden;
}

th,
td {
  border-radius: 5px;
}
